.go-button {
  margin-bottom: 45px;
  font-family: $bodyFont;
  font-weight: $medium;
  font-size: $mobileMainButton;
  border-radius: 22.5px;
  color: $orange;
  border: solid 2px $orange;
  background-color: $white;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 76px; 
  height: 45px;
  @include tablet {
    margin-bottom: 60px;
    width: 91px;}
  @include desktop {
    width: 160px;}
}